(function() {
  'use strict';

  angular
    .module('angular', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap']);

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($resource, url, urlPhotoSearch, urlPhotoOne) {
    var vm = this;
    vm.urlPhotoOne = url + urlPhotoOne;
    vm.photoCriteria = {
      name:""
      ,description: ""
    };

    var Photo = $resource(url + urlPhotoSearch, {});
    vm.refresh = function () {
      vm.items = Photo.query(vm.photoCriteria);
    }

    vm.handleEvent = function (event) {
      vm.refresh();
    }

    vm.refresh();
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope) {
    $rootScope.$on('$stateChangeError', function(){
      throw arguments[5];
    });

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('angular')
    //.constant("url", "http://127.0.0.1:8080")
    .constant("url", "")
    .constant("urlPhotoSearch", "/photo/search")
    .constant("urlPhotoOne", "/upload/")
  ;

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
  }

})();

angular.module("angular").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div class=\"container-fluid\"><div class=\"row well\"><div class=\"col-md-1\">id</div><div class=\"col-md-3\"><input id=\"name\" ng-model=\"main.photoCriteria.name\" ng-change=\"main.handleEvent($event)\" class=\"form-control\" type=\"search\" placeholder=\"Name:\"></div><div class=\"col-md-3\">CreateTime</div><div class=\"col-md-5\"><input id=\"description\" ng-model=\"main.photoCriteria.description\" ng-change=\"main.handleEvent($event)\" class=\"form-control\" type=\"search\" placeholder=\"Description:\"></div></div><div class=\"row\" ng-repeat=\"item in main.items\"><div class=\"col-md-1\">{{item.id}}</div><div class=\"col-md-3\">{{item.name}}</div><div class=\"col-md-3\">{{item.createTime | date : \"dd MMM yyyy H:mm\"}}</div><div class=\"col-md-4\">{{item.description}}</div><div class=\"col-md-1\"><div class=\"thumbnail\"><a ng-href=\"{{main.urlPhotoOne}}{{ item.name }}\"><img ng-src=\"{{main.urlPhotoOne}}{{ item.name }}\"></a></div></div></div></div></div>");}]);